import { defineDocumentType, makeSource } from 'contentlayer/source-files';

export const Activity = defineDocumentType(() => ({
  name: 'Activity',
  filePathPattern: `mos/**/*.mdx`,
  contentType: 'mdx',
  fields: {
    title: {
      type: 'string',
      required: true,
    },
    system: {
      type: 'string',
      required: true,
    },
    category: {
      type: 'string',
      required: true,
    },
    description: {
      type: 'string',
      required: true,
    },
  },
}));

export default makeSource({
  contentDirPath: 'content',
  documentTypes: [Activity],
  mdx: {
    rehypePlugins: [],
    remarkPlugins: [],
  },
});